import { Flex, Heading } from "@chakra-ui/layout"
import TextScramble from "@twistezo/react-text-scramble"
import * as React from "react"

const texts = ["web apps", "mobile apps", "websites"]

const Hero = () => {
  return (
    <>
      <Flex
        mx="20vw"
        justifyContent="normal"
        alignItems="center"
        flexDirection="column"
      >
        <Heading as="h1" fontSize="10vw" fontWeight="900" mt={"10vh"}>
          Assad Isah
        </Heading>
        <Heading as="h2" fontSize="3vw" fontWeight="600" mb="1vh">
          fullstack developer.
        </Heading>
        <Heading as="h2" fontSize="5vw" fontWeight="600" mb="10vh">
          <TextScramble
            texts={texts}
            letterSpeed={50}
            nextLetterSpeed={200}
            paused={false}
            pauseTime={1500}
            /* className={styles.scramble} */
          />
        </Heading>
      </Flex>
    </>
  )
}

export default Hero
