import { Image } from "@chakra-ui/image"
import { Box, Center, Divider, HStack, Text } from "@chakra-ui/layout"
import { Badge, Button, Flex, useBreakpointValue, Grid } from "@chakra-ui/react"
import { FiMonitor } from "@react-icons/all-files/fi/FiMonitor"
import { SiGithub } from "@react-icons/all-files/si/SiGithub"
import { graphql, useStaticQuery } from "gatsby"
import * as React from "react"

const Portfolio = () => {
  const col = useBreakpointValue({
    base: "1",
    sm: "1",
    md: "2",
    lg: "3",
  })
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        edges {
          node {
            frontmatter {
              appName
              appDesc
              techUsed
              liveLink
              githubLink
              imageLink
            }
          }
        }
      }
    }
  `)

  const portData = data.allMarkdownRemark.edges //[0].node.frontmatter
  return (
    <Box my="20vh">
      <Center>
        <Box
          id="projects"
          style={{
            content: " ",
            marginTop: "-100px",
            height: "100px",
            visibility: "hidden",
            pointerEvents: "none",
          }}
        ></Box>
        <Text fontWeight="700" fontFamily="Fira" fontSize="2vh">
          PROJECTS
        </Text>
      </Center>
      <Center my="2">
        <Divider my="2" w="7%" />
      </Center>
      <Grid
        templateColumns={`repeat(${col}, 1fr)`}
        gap={4}
        mb={4}
        boxSizing="border-box"
        px={12}
        mx={12}
      >
        {portData.map((data, index) => {
          return (
            <Flex flexDir="column">
              <Box
                key={index}
                borderWidth="1px"
                borderRadius="lg"
                overflow="hidden"
                boxShadow="0 0px 27px 0 rgb(0 0 0 / 10%), 0 4px 15px 0 rgb(0 0 0 / 20%)"
              >
                <Center my={2}>
                  <Text fontWeight="700"> {data.node.frontmatter.appName}</Text>
                </Center>
                <Box p="1" border="1px solid #e1e4e8">
                  <Image src={data.node.frontmatter.imageLink} alt="red" />
                </Box>
                <HStack justifyContent="center" mt={2} padding="5px">
                  <Button
                    as="a"
                    href={data.node.frontmatter.liveLink}
                    leftIcon={<FiMonitor />}
                    textDecoration="none"
                    bg="#e1e4e8"
                    target="_blank"
                  >
                    Live
                  </Button>
                  <Button
                    as="a"
                    href={data.node.frontmatter.githubLink}
                    leftIcon={<SiGithub />}
                    textDecoration="none"
                    bg="#e1e4e8"
                    target="_blank"
                  >
                    Code
                  </Button>
                </HStack>
                <Center>
                  <Divider my="2" w="70%" />
                </Center>
                <Flex p="1" justifyContent="normal" wrap="wrap">
                  {data.node.frontmatter.techUsed
                    .split(",")
                    .map((tech, index) => {
                      return (
                        <Badge
                          key={index}
                          borderRadius="full"
                          px="2"
                          colorScheme="teal"
                          m="1"
                        >
                          {tech}
                        </Badge>
                      )
                    })}
                </Flex>
                <Center>
                  <Divider my="2" w="70%" />
                </Center>
                <Box p="1" m="2">
                  <Text>{data.node.frontmatter.appDesc}</Text>
                </Box>
              </Box>
            </Flex>
          )
        })}
      </Grid>
    </Box>
  )
}

export default Portfolio
