import React from "react"
import Icon from "@chakra-ui/icon"
import { HStack, Link, Text } from "@chakra-ui/layout"
import { FaGithub } from "@react-icons/all-files/fa/faGithub"
import { FaTwitter } from "@react-icons/all-files/fa/FaTwitter"
import { FaLinkedin } from "@react-icons/all-files/fa/FaLinkedin"

import { Box, Center, Divider } from "@chakra-ui/react"
import Resume from "./resume"

const Contact = () => {
  return (
    <Box mx="20vw">
      <Box
        id="contact"
        style={{
          content: " ",
          marginTop: "-100px",
          height: "100px",
          visibility: "hidden",
          pointerEvents: "none",
        }}
      ></Box>
      <Center>
        <Text fontWeight="700" fontFamily="Fira" fontSize="2vh">
          CONTACT
        </Text>
      </Center>
      <Center my="2">
        <Divider my="2" w="7%" />
      </Center>
      <Center>
        <Text>Find me on: </Text>
      </Center>
      <Center>
        <HStack spacing={8} p="2">
          <Link
            href="https://www.github.com/nottherealalanturing"
            target="_blank"
          >
            <Icon as={FaGithub} w={12} h={12} />
          </Link>
          <Link
            color="twitter.500"
            href="https://www.twitter.com/assadeesaa"
            target="_blank"
          >
            <Icon as={FaTwitter} w={12} h={12} />
          </Link>
          <Link
            color="twitter.500"
            href="https://www.linkedin.com/in/assadisah/"
            target="_blank"
          >
            <Icon as={FaLinkedin} w={12} h={12} />
          </Link>
          <Link href="mailto:nottherealalanturing@gmail.com">
            <Icon viewBox="52 26 88 66" w={12} h={12} verticalAlign="baseline">
              <path
                fill="#4285f4"
                d="M58 108h14V74L52 59v43c0 3.32 2.69 6 6 6"
              />
              <path
                fill="#34a853"
                d="M120 108h14c3.32 0 6-2.69 6-6V59l-20 15"
              />
              <path
                fill="#fbbc04"
                d="M120 48v26l20-15v-8c0-7.42-8.47-11.65-14.4-7.2"
              />
              <path fill="#ea4335" d="M72 74V48l24 18 24-18v26L96 92" />
              <path
                fill="#c5221f"
                d="M52 51v8l20 15V48l-5.6-4.2c-5.94-4.45-14.4-.22-14.4 7.2"
              />
            </Icon>
          </Link>
        </HStack>
      </Center>
      <Center my={4}>
        <Resume />
      </Center>
    </Box>
  )
}

export default Contact
