import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
  Image,
  Center,
  Divider,
} from "@chakra-ui/react"
import React from "react"

const Resume = () => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <>
      <Button onClick={onOpen}>view resume</Button>

      <Modal
        isOpen={isOpen}
        onClose={onClose}
        closeOnOverlayClick={false}
        motionPreset="scale"
        scrollBehavior="outside"
        size="4xl"
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            <Center fontFamily="Fira" fontWeight="700" zIndex="1000">
              Resume
            </Center>
            <Center my="2">
              <Divider my="2" w="7%" />
            </Center>
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <Image src="/cv.jpg" alt="cv" w="100%" />
          </ModalBody>
          <ModalFooter>
            <Button colorScheme="blue" mr={3} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default Resume
